body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --pink-color: #ff7eee;
  --transition-speed: 600ms;
}

.dark {
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #26272b;
  --bg-clear: #454563;
  --pink-color: #ff7eee;
  --bg-secondary: #141418;
}

.light {
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #ffffff;
  --bg-secondary: #e4e4e4;
  --pink-color: #850073;
}
 
.theme-icon {
  display: none;
}

.dark #darkIcon {
  display: block;
}

.light #lightIcon {
  display: block;
}

.Home {
  margin: 0;
  padding: 0;
}

body {
  color: black;
  background-color: white;
  overflow-y: scroll;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background-color: black;
}

body::-webkit-scrollbar-thumb {
  background-color: #FFCAD4;
}

.App {
  margin-left: 5rem;
  background-color: #141418;
  background-color: var(--bg-secondary);
  color: #b6b6b6;
  color: var(--text-primary);
  min-height: 100vh;
}
 body {
    color: black;
    background-color: white;
    margin: 0;
    padding: 0;
  }
  
  body::-webkit-scrollbar {
    width: 0.25rem;
  }
  
  body::-webkit-scrollbar-track {
    background: #1e1e24;
  }
  
  body::-webkit-scrollbar-thumb {
    background: var(--pink-color);
  }
  
  .App {
    margin-left: 5rem;
    padding: 1rem;
  }
  
  .navbar {
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 600ms ease;
  }
  
  .navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .nav-item {
    width: 100%;
  }
  
  .nav-item:last-child {
    margin-top: auto;
  }
  
  .nav-link {
    display: flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    -webkit-filter: grayscale(100%) opacity(0.7);
            filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
  }
  
  .nav-link:hover {
    -webkit-filter: grayscale(0%) opacity(1);
            filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary);
  }
  
  .link-text {
    display: none;
    margin-left: 1rem;
  }
  
  .nav-link svg {
    font-size: 1.5rem;
    margin: 0 1.5rem;
  }
  
  .fa-primary {
    color: var(--pink-color);
  }
  
  .fa-secondary {
    color: #df49a6;
  }
  
  .fa-primary,
  .fa-secondary {
    transition: var(--transition-speed);
  }
  
  .logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align: center;
    color: var(--text-secondary);
    background: var(--bg-secondary);
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
  }
  
  .logo svg {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: var(--transition-speed);
  }
  
  .logo-text
  {
    display: inline;
    position: absolute;
    left: -999px;
    transition: var(--transition-speed);
    margin-left: 2rem;
  }
  
  .navbar:hover .logo svg {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  
  /* Small screens */
  @media only screen and (max-width: 600px) {
    .navbar {
      bottom: 0;
      width: 100vw;
      height: 5rem;
    }
  
    .logo {
      display: none;
    }
  
    .navbar-nav {
      flex-direction: row;
    }
  
    .nav-link {
      justify-content: center;
    }
  
    .App {
      margin: 0;
    }
  }
  
  /* Large screens */
  @media only screen and (min-width: 600px) {
    .navbar {
      top: 0;
      width: 5rem;
      height: 100vh;
    }
  
    .navbar:hover {
      width: 16rem;
    }
  
    .navbar:hover .link-text {
      display: inline;
    }
  
    .navbar:hover .logo svg
    {
      margin-left: 11rem;
    }
  
    .navbar:hover .logo-text
    {
      left: 0px;
    }
  }
.presentation {
    width: auto;
}

.flexbox-container {
    display: flex;
    align-items: center;
    margin: auto;
    justify-content: center;
}

.flexbox-item {
    align-self: center;
}

.name-h1 {
    font-family: "Rubik Maze";
    color: var(--pink-color);
}

.profile-pic {
    border-radius: 30%;
}

.bubble {
    background-color: var(--bg-primary);
    padding: 15px 25px;
    max-width: 450px;
    border-left: 5px solid;
    border-color: var(--pink-color);
    padding-bottom: 50px;
}

/* Large screens */
@media only screen and (min-width: 1100px) {
    .name-h1 {
        font-size: 8em;
        width: 520px;
    }

    .profile-pic {
        width: 304px;
        height: 304px;
    }

    .profile-pic {
        padding: 50px;
    }
}

/* Large screens */
@media only screen and (max-width: 1100px) {
    .name-h1 {
        font-size: 5.5em;
        width: 350px;
    }

    .profile-pic {
        width: 228px;
        height: 228px;
    }

    .profile-pic {
        padding: 30px;
    }
}

@media only screen and (max-width: 762px) {
    .flexbox-container {
        flex-direction: column;
        flex-flow: column-reverse;
    }

    .name-h1 {
        font-size: 6em;
        margin-top: 0;
    }

    .profile-pic {
        width: 200px;
        height: 200px;

    }
}
